/* Imports font from google fonts */
@import url('https://fonts.googleapis.com/css2?family=Palette+Mosaic&family=Poppins:wght@300;400;500;600&family=Roboto&display=swap');

/* This normalize all margins, etc in browsers */
*{
  margin:0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style:none;
  text-decoration: none;
}

:root {
  --color-bg: #021b20;
  --color-bg-variant: #37285c;
  --color-primary: #74B3CE;
  --color-primary-variant: rgba(80,137,145,.4);
  --color-white: #fff;
  --color-light: rgba(255,255,255,0.7);

  --transition: 400;

  --container-width-lg: 70%;
  --container-width-md: 85%;
  --container-width-sm: 90%;

}

html {
  scroll-behavior: smooth;
}


::-webkit-scrollbar {
  display:none;
}

body {
  background-color: var(--color-bg);
  font-family: 'Poppins', sans-serif;
  color: white;
  line-height: 1.7;
  background-color: #021b20;
  /* background-image: url(../src/assets/diagonal-striped-brick.png);
  background-attachment: fixed; */
}

/* Main Styles */
.main-container {
  align-items: center;
}
.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 5.5rem;
}

section > h2, section > h5 {
  text-align: center;
  color: var(--color-light)
}
section > h2 {
  color: var(--color-primary);
  margin-bottom: 2rem;
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-light);
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.5rem .75rem;
  /* border-radius: .4rem; */
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover {
  background: var(--color-light);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* Media Queries for Mobile Responsiveness */

@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }

  section > h2 {
    margin-bottom: 2rem;
  }
}
